@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@600&display=swap');

*{
  padding: 0;
  margin:0;
  box-sizing: border-box;
}
.content{
  height: 100vh;
}

.navbar{
  background-color:whitesmoke !important;
}
.register-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 50%;
  margin: 1rem auto;
  border: 1px solid black;
  border-radius: 10px;
  padding: 2rem;
}

/* transaction page*/
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
/* transaction page*/

  /* antd icons */
.switch-icons {
border: 1px solid lightgray;
border-radius: 5px;
padding: 10px 15px;
}
  .anticon svg{
  font-size: 20px;
  cursor:pointer;
}
.active-icon{
  color: darkslategray;
}
.inactive-icon{
  color:gray;
}

.btn {
  background-color: #867070;
  border-color: #867070;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.btn-danger:hover {
  background-color: #E4D0D0;
  color: #867070;
  border: 2px #867070 solid;
  font-size: 15px;
  font-weight: 500;
}

.btn-primary:hover {
  background-color: #E4D0D0;
  color: #867070;
  border: 2px #867070 solid;
  font-size: 15px;
  font-weight: 500;
}

.logo {
  color: #867070;
  font-size: 24px;
  font-weight: bolder;
  font-family: 'Libre Franklin', sans-serif;
  border: 3px #867070 solid;
  padding: 4px;
}

.footer {
  background-color: #867070;
}

.footer_text {
  color: #E4D0D0;
  font-weight: 500;
  font-size: 16px;
}